* {
  box-sizing: border-box;
}

body {
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  color: #212121;
  background-color: #fafafa;
  font-family: -apple-system, BlinkMacSystemFont, Segoe UI, Roboto, Oxygen-Sans, Ubuntu, Cantarell, Helvetica Neue, sans-serif;
  line-height: 1.5;
}

img {
  max-width: 100%;
  display: block;
}

.form {
  align-items: flex-end;
  gap: 10px;
  display: flex;
}

input {
  width: 200px;
  height: 40px;
  text-align: center;
  border: 1px solid #000;
  border-radius: 4px;
  padding: 5px;
  font-size: medium;
}

button {
  width: 100px;
  height: 40px;
  cursor: pointer;
  background-color: #a0fda0;
  border: 1px solid;
  border-radius: 5px;
  font-size: medium;
}

button:disabled {
  color: #b1b1b1;
  cursor: auto;
  background-color: #efeded;
  border: none;
}

.pause {
  background-color: #f4f458;
}

.destroy {
  background-color: tomato;
}

.timer {
  gap: 20px;
  margin-top: 50px;
  display: flex;
}

.field {
  width: 100px;
  border: 1px solid #ccc;
  border-radius: 4px;
  flex-direction: column;
  align-items: center;
  padding: 10px 0;
  display: flex;
}

.value {
  font-size: 40px;
  font-weight: 700;
}

.label {
  color: #ccc;
}

